<template>
   <v-layout>
    <v-flex xs12 sm6 offset-sm3>
      
      <v-list two-line>
          <template v-for="(item, index) in produtos">
            <v-card :key="index" class="mt">
              <v-img
                :src="item.img? item.img : 'http://ondasdosul.com.br/wp-content/themes/shopkeeper/images/no-image.jpg'"
                aspect-ratio="1.5"
              ></v-img>

              <v-card-title primary-title>
                <div>
                  <h3 class="headline mb-0">{{item.nome}}</h3>
                  <div>{{item.descricao}}</div>
                </div>
              </v-card-title>

            </v-card>
          </template>
        </v-list>
    </v-flex>
  </v-layout>
</template>

<script>
  import Agendamentos from '../components/Agendamentos'
  export default {
    components: {
      Agendamentos
    },
    data () {
      return {
        items: []
      }
    },
    computed:{
      produtos(){
        return this.$store.getters.produtos
      }

    },
    created(){
      this.$store.dispatch('produtos')
    }
  }
</script>
<style scoped>
 .mt{
   margin-top: 5px;
 }
</style>
