                <v-text-field prepend-icon="person" name="Username" label="Nome" class="mr-2 ml-2" v-model="produto.nome"></v-text-field>
                <v-text-field prepend-icon="book" name="Username" label="DescriÃ§Ã£o" class="mr-2 ml-2" v-model="produto.descricao"></v-text-field>
                <v-text-field prepend-icon="attachment" name="Username" label="Url Imagem" class="mr-2 ml-2" v-model="produto.img"></v-text-field>
