</template>
<script>
import { auth } from '../firebase'
  export default {
    data: () => ({
      produto:{
          nome:'',
          descricao:'',
          img: '',
      }
    }),
    computed:{
      usuario(){
          return this.$store.state.usuario
      }
    },
    mounted(){
        auth.onAuthStateChanged((user) =>{
            console.log('user', user)
        if (user) {
            
        } else {
            this.$router.push({
                name: 'login'
            })
        }
        });
    },
    methods:{
        cadastrar(){
            this.$store.commit('preloader')
            this.$store.dispatch('cadastrarProduto', this.produto)
            .then(()=>{
