
<template>
        <v-layout row class="text-xs-center fundo">
        <v-flex xs12 class="grey lighten-4">
            <v-container style="position: relative;top: 1%;" class="text-xs-center">
            <v-card flat>
                <v-card-title primary-title>
                <h4>Cadastrar</h4>
                </v-card-title>
                <v-form>
                <v-text-field prepend-icon="person" name="Username" label="Nome" class="mr-2 ml-2" v-model="professor.nome"></v-text-field>
                <v-text-field prepend-icon="book" name="Username" label="Disciplina" class="mr-2 ml-2" v-model="professor.disciplina"></v-text-field>
                <v-text-field prepend-icon="mail" name="Username" label="E-mail" class="mr-2 ml-2" v-model="professor.email"></v-text-field>
                <v-text-field prepend-icon="lock" name="Password" label="Senha" class="mr-2 ml-2" type="password" v-model="professor.senha"></v-text-field>
                <v-checkbox v-model="professor.admin" class="mr-5 ml-5" label="Administrador"></v-checkbox>
                <v-card-actions>
                <v-btn primary large block class="blue--text" v-on:click="cadastrar">Cadastrar</v-btn>
                </v-card-actions>
                </v-form>
            </v-card>
            </v-container>
        </v-flex>
        </v-layout>
</template>
<script>
import { auth } from '../firebase'
  export default {
    data: () => ({
      professor:{
          nome:'',
          email:'',
          senha: '',
          disciplina: '',
          admin: false
      },
      
    }),
    computed:{
     
    },
    mounted(){
        auth.onAuthStateChanged((user) =>{
            console.log('user', user)
        if (user) {
            
        } else {
            this.$router.push({
                name: 'login'
            })
        }
        });
    },
    methods:{
        cadastrar(){
            this.$store.commit('preloader')
            this.$store.dispatch('cadastrarProfessor',{
                professor: this.professor
            })
            .then(()=>{
                this.$snotify.success('Sucesso ao cadastrar')
                this.professor ={
                        nome:'',
                        email:'',
                        senha: '',
                        disciplina: ''
                }
                this.$store.commit('preloader')
            })
            .catch(()=>{
                this.$snotify.error('Erro ao cadastrar, tente mais tarde!')
                this.$store.commit('preloader')
            })
        }
    }

  }
</script>

<style>
.fundo{
    width:100%;
    background-color: #DCEDC8;
}
.acessar{
    font-size: 12pt;
    text-decoration: none;
}

</style>
